#experience{
    min-height: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}

.expTitle{
    font-size: 3rem;
    font-weight: 600;
    padding-top: 3rem;
    margin-bottom: 1.5rem;
}

.expDesc{
    font-weight: 400;
    font-size: 1rem;
    max-width: 71rem;
    padding: 0 2 rem;
}

.expDiv{
    width: 100vw;
    max-width: 80%;
    text-align: left;
    align-self: center;
}

.expRow{
    display: flex;
    flex-direction: row;
    margin: 1rem;
    padding: 1.5rem 2rem;
    border-radius: 0.5rem;
    background: rgb(50, 50, 50);
}
.expImage{
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin-right: 2rem;
}

.expText>p{
    font-size: 0.9rem;
    font-weight: 300;
    text-align: right;
}

.expDet{
    max-width: 99%;
    font-size: 0.8rem;
    font-weight: 300;
    text-align: justify;
}