#portfolio{
    margin: 0 auto; 
    min-width: calc(100vh-4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}

.portTitle{
    margin: 1rem 0;
    font-size: 3rem;
}

.portDesc{
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;
    margin-bottom: 2rem;
}

.portImages{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}

.portImg{
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
}

.portBtn{
    margin: 2rem 0;
    padding: 0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: 1rem;
}

@media screen and (max-width: 585px) {
    .portImg{
        height: 45vw;
    }
}